/* HomePage.css */
html, body {
    overflow: hidden;
    /* height: 100vh; */
}

  .home-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #121212;
  }
  
  h1 {
    font-size: 36px;
    color: #ffffff;
    margin-bottom: 50px;
  }
  
  .button-container {
    display: flex;
    justify-content: center;
    gap: 20px;
  }
  
  .home-button {
    width: 150px;
    background-color: #6200ea;
    color: #ffffff;
    border: none;
    padding: 15px 30px;
    font-size: 18px;
    cursor: pointer;
    border-radius: 10px;
    transition: background-color 0.3s ease, transform 0.2s ease;
  }
  
  .home-button:hover {
    background-color: #3700b3;
    transform: scale(1.05);
  }